import React, { useState, useEffect } from 'react'
import Mark from "../mark.png"
export default function AutoSearch(props) {

  const [Search, setSearch] = useState(null);
  const [City, setCity] = useState(null)
  const [Loading, setLoading] = useState(true)

  let handleOnChange = (event) => {
    setSearch(event.target.value)
  }


  useEffect(() => {

    const fetchCity = async () => {
      const url = `https://api.geoapify.com/v1/geocode/autocomplete?text=${Search}&limit=3&apiKey=8941dc138f9e4df29fa529403c7dce89`;
      let data = await fetch(url);
      let ParsedData = await data.json();
      setCity(ParsedData.features);
      setLoading(false)
    }


    fetchCity().catch((reason) => {
      console.error('ERROR : ' + { reason })
      return (
        <div>
          <h1>error: {reason}</h1>
        </div>
      )
    })
  }, [Search])

  return (
    <div className="main-wrapper">
      <div className="input" style={{ background: '#0066ff', display: 'flex', alignItems: 'center', justifyContent: 'center', paddingTop: '20px' }}>
        <input type="text" placeholder="Search..." onChange={handleOnChange} style={{ background: 'white', color: 'black', margin: '0px', fontFamily: 'poppins, sans-serif', width: '90%', border: 'none', outline: 'none', fontSize: '20px', padding: '10px', borderRadius: '10px' }} />
      </div>
      {Loading ? null : Search === null ? null : !City ? null : (
        <div className="searches" style={{background: '#0066ff', display: 'flex', alignItems: 'center', justifyContent: 'center', paddingTop: '20px', zIndex: 1000}}>
          <div className="wrapper" style={{background: 'white', width: '90%', borderRadius: '20px'}}>
          {City.map((e) => {
            return <div className="textbox" style={{display: 'flex', alignItems: 'flex-start', justifyContent: 'flex-start', cursor: 'pointer', paddingTop: '10px', paddingLeft: '5px'}} onClick={
              ()=>{
                props.func2(e.properties.lat, e.properties.lon)
              }
            }>
              <img src={Mark} alt="marker" style={{height: '30px', paddingRight: '10px'}}/><p>{e.properties.county}, {e.properties.state}, {e.properties.country}</p>
            </div>
          })}
          </div>
        </div>)
      }
    </div>
  )
}
