
import AutoSearch from './Search'
export default function Current(props) {

    return (
        <div>
            {props.loading ? (<div className="container" style={{display: 'flex', alignItems: 'center', justifyContent: 'center', height: '90vh'}}><div className="progress" style={{width: '100%'}}>
  <div class="progress-bar progress-bar-striped bg-success" role="progressbar" style={{width: props.progress}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
</div></div>) : (
                <div>
                <AutoSearch func2 = {props.func1} ></AutoSearch>
                <div className="main-container" style={{ height: '100vh', background: `linear-gradient(#0066ff,#003d99, #002966)` }}>
                    <div className="city">
                        <h1 className="text-center" style={{ color: 'white', fontFamily: 'poppins, sans-serif', padding: '25px' }}>{props.country.name}, {props.country.sys.country}</h1>
                    </div>
                    <div className="container">
                        <div className="weather-box" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-evenly' }}>
                            <div className="conditions">
                                <p style={{ color: 'white', fontFamily: 'poppins, sans-serif', textAlign: 'left' }}>&#8593; {Math.round(props.temp.temp_max)}<sup>°C</sup>  &#8595; {Math.round(props.temp.temp_min)}<sup>°C</sup></p>
                                <h1 style={{ fontSize: '70px', color: 'white', fontFamily: 'poppins, sans-serif' }}>{Math.round(props.temp.temp)}<sup>°C</sup></h1>
                                <h1 style={{ fontSize: '25px', color: 'white', fontFamily: 'poppins, sans-serif', textAlign: 'left' }}>{props.weather.map((e) => { return <h1 id="main-weather">{e.main}</h1> })}</h1>
                            </div>
                            <div className="icon">
                                <img src={props.weather.map((e) => { return `http://openweathermap.org/img/wn/${e.icon}@2x.png` })} alt="icon" style={{ height: '25vh', paddingLeft: '30px'}} id="icon-image" />
                            </div>
                        </div>
                    </div>
                </div>
</div>
            )}
        </div>
    )
}


