
import Current from './Components/Current';
import React, { useState, useEffect } from 'react'


function App() {

  const [Weather, setWeather] = useState(null)
  const [Loading, setLoading] = useState(true)
  const [Temp, setTemp] = useState(null)
  const [Country, setCountry] = useState(null)
  const [Lat, setLat] = useState('52.517036');
  const [Lon, setLon] = useState('13.3888599');
  const [Progress, setProgress] = useState(25)


  useEffect(() => {
    let fetchData = async () => {
      setLoading(true)
      const url = `https://api.openweathermap.org/data/2.5/weather?lat=${Lat}&lon=${Lon}&appid=22e389a193bc53b0833c299dd5786757&units=metric`;
      setProgress(25)
      let data = await fetch(url);
      setProgress(50)
      let jsonData = await data.json();
      setProgress(75)
      setWeather(jsonData.weather);
      setTemp(jsonData.main);
      setCountry(jsonData);
      setProgress(100)
      setLoading(false);
    }

    fetchData()
  }, [Lat, Lon])

  const FetchCityData = (lat, lon) => {
    setLat(lat);
    setLon(lon)
  }




  return (
    <Current loading={Loading} weather={Weather} temp={Temp} country={Country} func1={FetchCityData} progress={Progress}></Current>
  );
}

export default App;
